export class Dog {
    constructor(public breed: string, public subBreeds: string[]){};

    hasSubBreeds():boolean {
        return this.subBreeds.length > 0;
    }

    hasBreedFragment(breedStr):boolean {
        let result:boolean = false;
        if (this.breed.includes(breedStr))
            result = true;
        else if (this.hasSubBreeds()) {
            result = this.subBreeds.some(sub => sub.includes(breedStr));
        }
        return result;
    }
}


