import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Dog } from '../models/dog';
import { environment as env } from '../../environments/environment';

@Injectable()
export class DogService {
  // TODO: pull this into a config/environment file
  private breedsApiFrag:string = '/api/breeds/list/all';
  private imgApiFrag:string = '/api/breed/';
  breeds:Dog[] = [];

  constructor(private http:HttpClient) {
    this.http.get(this.breedsApiFrag)
      .subscribe(res => this.arrayifyResponse(res));
  }

  arrayifyResponse(response:object) {
    if (!response.hasOwnProperty("message")) {
      alert("error, no breeds fetched!");
      return;
    }

    let breedsObj = response['message'];
    for (let breed in breedsObj) {
      let dogObj = new Dog(breed, breedsObj[breed]);
      this.breeds.push(dogObj);
    }
  }

  getAllBreeds():Dog[] {
    return this.breeds;
  }

  getBreed(breed:string) {
    return this.http.get<string[]>(this.imgApiFrag + breed + "/images");
  }

  getSubBreed(breed:string, subBreed:string) {
    return this.http.get<string[]>(this.imgApiFrag + breed + "/" + subBreed + "/images");
  }

  filterBreed(breedStr:string):Dog[] {
    return this.breeds.filter(breed => breed.hasBreedFragment(breedStr));
  }
}



